.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 20;
  background-color: rgba(0, 0, 0, 0.75);
}

.modal {
  position: fixed;
  top: 25vh;
  left: 15%;
  width: 70%;
  max-height: 70vh;
  background-color: #F5EDC6;
  padding: 1rem;
  border-radius: 14px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
  z-index: 30;
  animation: slide-down 300ms ease-out forwards;
}

@media (max-width: 600px) {
  .modal {
    width: 100%;
    left:0;
    border-radius: 0;
    min-height: 30vh;
  }
}

