.header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  color: #F9F9F9;
  background-color: rgba(0, 0, 0, 0.4);
  -webkit-box-shadow: 1px 15px 27px 15px rgba(0, 0, 0, 0.4);
  box-shadow: 1px 15px 27px 15px rgba(0, 0, 0, 0.4);
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1% 5%;
}

.navigation {
  display: flex;
  width: 30%;
  justify-content: space-between;
  align-items: center;
}

.navigation > * {  
  cursor: pointer;
}

.main-image {
  width: 100%;
  height: 40rem;
  z-index: 0;
  overflow: hidden;
}

.main-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

a {
  color: white;
  text-decoration:none;
}